.order-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px 0;
  width: 400px;
}
.order-placed {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  letter-spacing: 0.4px;
  color: #2c2e2f;
  margin: 10px 0;
}
.order-info {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  text-align: center;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}

.fees-container {
  width: 100%;
  height: 96px;
  background: #f7f7f7;
  padding: 20px 40px;
}
.fees-description {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.fees-description p {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}
/* hr.r{
    border-top: 1px solid #E3E3E3;
} */
.hr {
  border-top: 1px solid #e3e3e3;
  width: 100%;
  height: 10px;
  line-height: 80%;
  margin-bottom: 10px;
}
.delivery-container {
  padding: 0px 40px;
}
.delivery-title {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4px;
  color: #2c2e2f;
  width: 100%;
  padding: 20px 40px 0 40px;
}
.delivery-body {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}
.order-button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px 18px;
  gap: 8px;
  width: 90%;
  height: 44px;
  background: var(--primary-color);
  border: 1px solid var(--primary-color);
  box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
  border-radius: 8px;
  color: #ffffff;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.order-items {
  display: flex;
  gap: 20px;
  align-items: flex-start;
  margin: 40px 0 20px 0;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
}
.order-items-img {
  max-height: 120px;
  width: 120px;
  object-fit: contain;
}
.order-item-details {
  padding: 5px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  gap: 20px;
}
.order-item-name {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}
.order-item-qty {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}
.order-item-qty span {
  font-style: normal;
  font-weight: 400;
  font-size: 19px;
  line-height: 28px;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}
.order-item-price {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.4px;
  color: #2c2e2f;
}

@media screen and (max-width: 425px) {
  .order-items,
  .delivery-container,
  .fees-container {
    padding: 20px 8px;
  }
  .order-item-name,
  .order-item-price {
    font-size: 16px;
  }
  .fees-description p {
    font-size: 14px;
  }
}
