.hero-section {
  position: relative;
}

.hero-img {
  background-size: cover;
  background-repeat: no-repeat;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.store-initails {
  margin-top: -100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.store-initails h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 180px;
  background: #d9d9d9;
  font-style: normal;
  font-weight: 600;
  font-size: 64px;
  line-height: 77px;
  letter-spacing: 0.4px;
  border-radius: 50%;
  color: #464646;
}

.store-wrapper {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.store-title {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  letter-spacing: 0.4px;
  color: var(--black-color);
}

.store-description {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  text-align: center;
  letter-spacing: 0.4px;
  color: var(--black-color);
  width: 70%;
  margin: 20px;
}

.search-wrapper {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.serach-container {
  background: #fbfbfb;
  border: 1px solid var(--primary-color);
  border-radius: 20px;
  height: 52px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-icon {
  color: rgba(44, 49, 73, 0.5);
  margin: 0 12px;
  font-size: 18px;
}

.search-input {
  flex: 1;
  height: 50px;
  border: none;
  font-size: 16px;
  background: #fbfbfb;
}

.search-btn {
  background-color: var(--primary-color);
  border-radius: 20px;
  border: none;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  width: 94px;
  height: 52px;
  margin-left: 5px;
}

::placeholder,
::-moz-placeholder,
::-webkit-placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  text-transform: capitalize;
  color: #73797f;
}

.products-container {
  padding: 100px 0 100px 0;
}

.products-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.products-title {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: var(--black-color);
  letter-spacing: 0.4px;
}

.product-feature {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.4px;
  color: var(--black-color);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.product-feature-icon {
  width: 16px;
}

@media screen and (max-width: 425px) {
  .store-initails {
    margin-top: -80px;
  }

  .store-initails h1 {
    width: 140px;
    height: 140px;
    font-size: 50px;
  }

  .store-title {
    text-align: center;
    font-size: 26px;
  }

  .store-description {
    width: 100%;
  }

  .products-title {
    font-size: 25px;
  }

  .products-header {
    align-items: flex-start;
    margin-bottom: 20px;
    flex-direction: column;
    gap: 10px;
  }
}

@media screen and (max-width: 768px) {
  .store-description {
    width: 90%;
  }

  .store-initails h1 {
    height: 160px;
    width: 160px;
    font-size: 54px;
  }

  .products-title {
    font-size: 28px;
  }

  .product-feature {
    font-size: 16px;
  }

  .product-feature-icon {
    width: 14px;
  }
}
